import { NextResponse } from "next/server";
import { v4 as uuidv4 } from "uuid";

const GTK_URL = "https://api.ecoledirecte.com/v3/login.awp?gtk=1&v=4.75.0";
const LOGIN_URL = "https://api.ecoledirecte.com/v3/login.awp?v=4.75.0";

export async function POST(req: Request) {
  try {
    const body = await req.json();
    const identifiant = body?.identifiant || process.env.ECOLE_DIRECTE_USER;
    const motdepasse = body?.motdepasse || process.env.ECOLE_DIRECTE_PASS;

    if (!identifiant || !motdepasse) {
      return NextResponse.json(
        { error: "⚠️ Identifiant et mot de passe requis" },
        { status: 400 }
      );
    }
    const gtkRes = await fetch(GTK_URL, {
      headers: {
        "Origin": "https://www.ecoledirecte.com",
        "Referer": "https://www.ecoledirecte.com/",
        "User-Agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 Chrome/109.0.0.0 Safari/537.36"
      },
    });
    const gtkCookie = gtkRes.headers.get("set-cookie")?.match(/GTK=([^;]+)/)?.[1];
    if (!gtkCookie) {
      return NextResponse.json({ error: "⚠️ Impossible de récupérer GTK" }, { status: 500 });
    }
    const uuid = uuidv4(); // UUIDv4 valide
    const loginPayload = `data=${encodeURIComponent(
      JSON.stringify({
        identifiant,
        motdepasse,
        isRelogin: false,
        uuid
      })
    )}`;

    // 3️⃣ Requête POST login
    const loginRes = await fetch(LOGIN_URL, {
      method: "POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
        "X-Gtk": gtkCookie,
        "Origin": "https://www.ecoledirecte.com",
        "Referer": "https://www.ecoledirecte.com/",
        "User-Agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 Chrome/109.0.0.0 Safari/537.36"
      },
      body: loginPayload,
    });

    const loginData = await loginRes.json();
    if (loginData?.code === 200 && loginData?.token) {
      return NextResponse.json({
        message: "✅ Connexion réussie",
        token: loginData.token,
        accounts: loginData.data?.accounts ?? []
      });
    } else if (loginData?.code === 250) {
      return NextResponse.json({ error: "⚠️ Double-auth requise", details: loginData }, { status: 401 });
    } else {
      return NextResponse.json(
        { error: "❌ Identifiants invalides", details: loginData },
        { status: 401 }
      );
    }

  } catch (err: any) {
    return NextResponse.json(
      { error: "⚠️ Erreur serveur", details: err.message },
      { status: 500 }
    );
  }
}
